.App {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 100vh;
  margin: 0;
  background-color: #f4f4f4;
}

.board-container {
  display: flex;
  align-items: flex-start;
}

.board {
  display: grid;
  grid-template-columns: repeat(15, 30px);
  grid-template-rows: repeat(15, 30px);
  gap: 1px;
  margin-right: 20px;
  border: 2px solid #000;
  background-color: #fff;
  border-radius: 10px;
}

.cell {
  width: 30px;
  height: 30px;
  background-color: #fff;
  border: 1px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: 50%; /* 使棋子变成圆形 */
}

.cell.black {
  background-color: blue; /* 蓝色 */
}

.cell.white {
  background-color: purple; /* 紫色 */
}

button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 5px;
  background-color: #fff;
  margin-top: 20px;
}

button:hover {
  background-color: #f0f0f0;
}

h3 {
  margin-top: 30px;
}

ul {
  list-style-type: none;
  padding: 0;
  margin-top: 10px;
}

ul li {
  margin: 5px 0;
}

ul button {
  font-size: 14px;
}

.history-container {
  width: 200px;
  max-height: 400px;
  overflow-y: auto;
}

.history-button {
  margin-left: 20px;
}

/* 当前执棋方的显示 */
.current-player {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}
.reset-button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 5px;
  background-color: #ff5733; /* 红色背景 */
  color: white;
  margin-top: 20px;
  display: block;
  width: 200px;
  margin-left: auto;
  margin-right: auto;
}

.reset-button:hover {
  background-color: #ff7043; /* 红色按钮 hover 状态 */
}
